/* 기본 색상 (라이트 모드) */
:root {
  --background-color: #f2f2f2;
  --text-color: #0d0d0d;
  --purple: #bdb2ff;
  --purple-dark: #4a4d8f;
  --blue: #a0c4ff;
  --blue-dark: #2667a8;
  --gray-700: #262626;
  --gray-600: #404040;
  --gray-500: #b3b3b3;
  --gray-400: #d9d9d9;
  --gray-300: #e6e6e6;
  --gray-200: #ffffff;
  --gray-100: #ffffff;
  --danger: #f67280;
}

/* 다크 모드 색상 */
[data-theme='dark'] {
  --background-color: #0d0d0d;
  --text-color: #f2f2f2;
  --purple: #5e60ce;
  --purple-dark: #8284fa;
  --blue: #1e6f9f;
  --blue-dark: #4ea8de;
  --gray-700: #f2f2f2;
  --gray-600: #d9d9d9;
  --gray-500: #808080;
  --gray-400: #333333;
  --gray-300: #262626;
  --gray-200: #1a1a1a;
  --gray-100: #0d0d0d;
  --danger: #e25858;
}

body {
  background-color: var(--background-color);
  color: var(--text-color);
  font-family: Arial, sans-serif;
}

h1,
h2,
h3 {
  color: var(--text-color);
}

.purple {
  background-color: var(--purple);
}
.purple-dark {
  background-color: var(--purple-dark);
}
.blue {
  background-color: var(--blue);
}
.blue-dark {
  background-color: var(--blue-dark);
}

.gray-700 {
  background-color: var(--gray-700);
}
.gray-600 {
  background-color: var(--gray-600);
}
.gray-500 {
  background-color: var(--gray-500);
}
.gray-400 {
  background-color: var(--gray-400);
}
.gray-300 {
  background-color: var(--gray-300);
}
.gray-200 {
  background-color: var(--gray-200);
}
.gray-100 {
  background-color: var(--gray-100);
}

.danger {
  background-color: var(--danger);
}
